@use "../../styles/mixins/adaptivity.scss";
@import "../../styles/mixins/adaptivity.scss";

.result-section {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px 0;

  &__input {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
  }

  @include adaptivity(900px) {
    padding: 20px 50px;
    max-width: 1000px;
  }

  @include adaptivity(600px) {
    padding: 20px 25px;
  }

  &__title {
    display: flex;
    justify-content: center;
    background-image: linear-gradient(
      146deg,
      rgba(221, 26, 253, 1) 6%,
      rgba(36, 240, 249, 1) 64%
    );
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__item {
    width: 500px;
    padding: 15px;
    border-radius: 20px;
    align-items: center;
    position: relative;
    box-sizing: border-box;
    $border: 0px;
    color: #fff;
    background: transparent;
    background-clip: padding-box;
    border: solid $border transparent;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
      margin: -$border;
      border-radius: inherit;
      background: radial-gradient(
        circle,
        rgba(255, 201, 141, 1) 41%,
        rgba(29, 255, 196, 1) 100%
      );
    }

    &-title {
      background-image: linear-gradient(
        90deg,
        rgba(131, 58, 180, 1) 0%,
        rgba(253, 29, 29, 1) 74%,
        rgba(252, 176, 69, 1) 100%
      );
      color: transparent;
      -webkit-background-clip: text;
      background-clip: text;

      &_thyme_dark {
        background-image: linear-gradient(
          52deg,
          rgb(235, 253, 26) 0%,
          rgb(249, 104, 36) 100%
        );
      }
    }
  }

  &__item-image {
    background-size: cover;
    width: 150px;
    height: 150px;
    border-radius: 10px;
  }
}

.description {
  &_thyme_dark {
    color: white;
  }
}

.ui.items > .item > .image > img {
  border-radius: 10px;
}

.ui.items:not(.unstackable) > .item {
  @media only screen and (max-width: 767px) {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin: 0;
  }
}

.ui.items > .item > .content > .description {
  &_thyme_dark {
    color: white;
  }
}
